import React, { Component } from "react";
import AudioVisualizer from "./audiovisualizer";

class Spectrogram extends Component {
  constructor(props) {
    super(props);
    this.state = {
      audioData: ""
    };
  }

  componentDidMount() {
    console.log(this._audioElement);

    this.audioContext = new (window.AudioContext ||
      window.webkitAudioContext)();
    this.track = this.audioContext.createMediaElementSource(this._audioElement);
    // this.masterGain = this.audioContext.createGain();
    this.analyser = this.audioContext.createAnalyser();
    this.track
      // .connect(this.masterGain)
      .connect(this.analyser)
      .connect(this.audioContext.destination);
    // this.analyser.fftSize = 2048;
    // var bufferLength = this.analyser.frequencyBinCount;
    // var dataArray = new Uint8Array(bufferLength);
    // this.analyser.getByteTimeDomainData(dataArray);
    // console.log(dataArray);
    // this.setState({ audioData: dataArray });
  }

  componentWillUnmount() {
    this.analyser.disconnect();
  }

  render() {
    return (
      <>
        <audio
          // crossOrigin="anonymous"
          controls
          ref={audioElement => (this._audioElement = audioElement)}
        >
          <source src={this.props.audioUrl} type="audio/wav" />
          Your browser does not support the audio element.
        </audio>

        {/* <AudioVisualizer audioData={this.state.audioData} /> */}
        {/* <textarea value={this.state.audioData} />; */}
      </>
    );
  }
}

export default Spectrogram;
