import React, { Component } from "react";
import {
  CssBaseline,
  AppBar,
  IconButton,
  Toolbar,
  Typography
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { withStyles } from "@material-ui/styles";
import HomeOutlinedIcon from "@material-ui/icons/HomeOutlined";

import SignOutButton from "../SignOut";
import { AuthUserContext } from "../Session";
import * as ROUTES from "../../constants/routes";

const styles = theme => ({
  root: {
    display: "flex"
  },
  menuButton: {
    marginRight: theme.spacing(2)
  },
  title: {
    flexGrow: 1
  }
});

const Dashboard = () => (
  <div>
    <AuthUserContext.Consumer>
      {authUser => (authUser ? <DashboardAuth /> : <DashboardNonAuth />)}
    </AuthUserContext.Consumer>
  </div>
);

class DashboardAuthBase extends Component {
  render() {
    const { classes } = this.props;

    return (
      <div className={classes.root}>
        <CssBaseline />
        <AppBar position="static">
          <Toolbar>
            <Typography variant="h6" className={classes.title}>
              Seeing is Believing
            </Typography>
            <IconButton
              component={Link}
              to={ROUTES.LOADING}
              edge="start"
              color="inherit"
              aria-label="home"
            >
              <HomeOutlinedIcon fontSize="large" />
            </IconButton>
            <SignOutButton />
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}

const DashboardAuth = withStyles(styles)(DashboardAuthBase);

class DashboardNonAuthBase extends Component {
  render() {
    const { classes } = this.props;

    return (
      <div className={classes.root}>
        <CssBaseline />
        <AppBar position="static">
          <Toolbar>
            <Typography variant="h6" className={classes.title}>
              Seeing is Believing
            </Typography>
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}

const DashboardNonAuth = withStyles(styles)(DashboardNonAuthBase);

export default Dashboard;
