import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { withStyles } from "@material-ui/styles";
import { Grid, Typography, Container } from "@material-ui/core";

import { withFirebase } from "../Firebase";
import { compose } from "recompose";
import SimpleCard from "../CardFunction";
import * as ROUTES from "../../constants/routes";
import { withAuthorization } from "../Session";
import * as ROLES from "../../constants/roles";

const styles = theme => ({
  root: {
    height: "100vh"
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8)
  },
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column"
  },
  cardMedia: {
    paddingTop: "56.25%" // 16:9
  },
  cardContent: {
    flexGrow: 1
  }
});
class StudentPageBase extends Component {
  render() {
    const { classes } = this.props;

    return (
      <div>
        <Container className={classes.cardGrid} maxWidth="md">
          <Grid container spacing={4}>
            <SimpleCard
              image="/images/homework.jpg"
              title="first title"
              link={ROUTES.STUDENT_HOMEWORKS}
            >
              <Typography gutterBottom variant="h5" component="h2">
                Homework
              </Typography>
              <Typography>Your Homeworks</Typography>
            </SimpleCard>
            <SimpleCard
              image="/images/word.jpg"
              title="second title"
              link={ROUTES.WORD_LIST}
            >
              <Typography gutterBottom variant="h5" component="h2">
                List of Words
              </Typography>
              <Typography>Practice your pronunciation</Typography>
            </SimpleCard>
          </Grid>
        </Container>
      </div>
    );
  }
}

const condition = authUser => !!authUser && authUser.role === ROLES.STUDENT;

const StudentPage = compose(
  withRouter,
  withFirebase,
  withStyles(styles),
  withAuthorization(condition)
)(StudentPageBase);

export default StudentPage;
