import React, { Component, createRef } from "react";
import ReactDOM from "react-dom";
import { compose } from "recompose";
import { withRouter } from "react-router-dom";
import { withFirebase } from "../Firebase";
import { withStyles } from "@material-ui/styles";
import {
  Grid,
  TextField,
  Select,
  InputLabel,
  Button,
  Typography,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
  List,
  ListItem
} from "@material-ui/core";
import WaveSurfer from "wavesurfer.js";
import SpectrogramPlugin from "wavesurfer.js/dist/plugin/wavesurfer.spectrogram.min.js";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

import * as ROUTES from "../../constants/routes";
import * as COLORMAPS from "../../constants/colormaps";

const styles = theme => ({
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  input: {
    display: "none"
  },
  spectrogram: {
    height: "300px",
    overflow: "hidden"
  },
  panel: {
    width: "100%"
  }
});

const INITIAL_STATE = {
  dialect: "",
  selectedLanguage: "",
  error: null
};

class DialectFormBase extends Component {
  constructor(props) {
    super(props);

    this.state = { ...INITIAL_STATE };
  }

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  onSubmit = event => {
    const { selectedLanguage, dialect } = this.state;

    this.props.firebase
      .dialects(selectedLanguage)
      .push({
        dialect
      })
      .then(() => {
        this.setState(oldState => ({
          ...INITIAL_STATE,
          selectedLanguage: oldState.selectedLanguage
        }));
      })
      .catch(error => {
        this.setState({ error });
      });

    event.preventDefault();
  };

  render() {
    const { languages, classes } = this.props;

    const { dialect, selectedLanguage, error } = this.state;

    let dialects = [];
    if (!!selectedLanguage) {
      dialects = languages.find(lan => lan.languageId === selectedLanguage)
        .dialects;
    }
    const isInvalid = dialect === "" || selectedLanguage === "";

    return (
      <form className={classes.form} noValidate onSubmit={this.onSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <InputLabel required id="languageLabel">
              Language
            </InputLabel>
            <Select
              native
              value={selectedLanguage}
              name="selectedLanguage"
              labelId="languageLabel"
              variant="outlined"
              fullWidth
              onChange={this.onChange}
            >
              <option value="" />
              {languages &&
                languages.map(language => (
                  <option key={language.languageId} value={language.languageId}>
                    {language.name}
                  </option>
                ))}
            </Select>
          </Grid>
          {selectedLanguage !== "" && (
            <Grid item xs={12}>
              <ExpansionPanel className={classes.panel}>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                  <Typography>Available dialects</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                  <List>
                    {Object.keys(dialects).map(dialectId => (
                      <ListItem key={dialectId}>
                        {dialects[dialectId].dialect}
                      </ListItem>
                    ))}
                  </List>
                </ExpansionPanelDetails>
              </ExpansionPanel>
            </Grid>
          )}
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              autoFocus
              fullWidth
              id="dialect"
              label="Dialect"
              name="dialect"
              value={dialect}
              onChange={this.onChange}
            />
          </Grid>
          <Grid item xs={12}>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              disabled={isInvalid}
            >
              Add
            </Button>
            {error && <Typography color="error">{error.message}</Typography>}
          </Grid>
        </Grid>
      </form>
    );
  }
}

const DialectForm = compose(
  withRouter,
  withFirebase,
  withStyles(styles)
)(DialectFormBase);

export default DialectForm;
