import React, { Component } from "react";
import { compose } from "recompose";
import { withStyles } from "@material-ui/styles";
import {
  Container,
  CssBaseline,
  Typography,
  Tabs,
  Tab,
  Box,
  AppBar
} from "@material-ui/core";
import LanguageForm from "./languageform";
import DialectForm from "./dialectform";
import { withFirebase } from "../Firebase";

const styles = theme => ({
  root: {
    height: "100vh"
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  }
});

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

const INITIAL_STATE = {
  tabValue: 0,
  loading: false,
  languages: []
};

class LanguagePageBase extends Component {
  constructor(props) {
    super(props);
    this.state = { ...INITIAL_STATE };
  }

  componentDidMount() {
    this.props.firebase.languages().on("value", snapshot => {
      const languagesObject = snapshot.val();

      const languagesList = Object.keys(languagesObject).map(key => ({
        ...languagesObject[key],
        languageId: key
      }));

      this.setState({
        languages: languagesList,
        loading: false
      });
    });
  }

  componentWillUnmount() {
    this.props.firebase.languages().off();
  }

  onChangeTab = (event, tabValue) => {
    this.setState({ tabValue });
  };

  render() {
    const { classes } = this.props;

    const { tabValue, languages } = this.state;

    return (
      <Container component="main" maxWidth="sm" className={classes.root}>
        <CssBaseline />
        <div className={classes.paper}>
          <Typography component="h1" variant="h5">
            Add a New
          </Typography>
          <Tabs
            value={tabValue}
            name="tabValue"
            onChange={this.onChangeTab}
            indicatorColor="primary"
            textColor="primary"
            variant="fullWidth"
            aria-label="add a new language/dialect"
          >
            <Tab label="Language" value={0}></Tab>
            <Tab label="Dialect" value={1}></Tab>
          </Tabs>

          <TabPanel value={tabValue} index={0}>
            <LanguageForm />
          </TabPanel>
          <TabPanel value={tabValue} index={1}>
            <DialectForm languages={languages} />
          </TabPanel>
        </div>
      </Container>
    );
  }
}

const LanguagePage = compose(
  withStyles(styles),
  withFirebase
)(LanguagePageBase);

export default LanguagePage;
