import React, { Component, createRef } from "react";
import { compose } from "recompose";
import { withRouter } from "react-router-dom";
import { withFirebase } from "../Firebase";
import { withStyles } from "@material-ui/styles";
import {
  Grid,
  TextField,
  Select,
  InputLabel,
  Button,
  Typography,
  Fab,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
  ExpansionPanelActions,
  Dialog,
  DialogTitle,
  LinearProgress,
  DialogContent,
  DialogContentText
} from "@material-ui/core";
import WaveSurfer from "wavesurfer.js";
import SpectrogramPlugin from "wavesurfer.js/dist/plugin/wavesurfer.spectrogram.min.js";
import AddIcon from "@material-ui/icons/Add";

import "../../index.css";

import UploadingDialog from "../uploadingDialog";
import * as COLORMAPS from "../../constants/colormaps";
import * as ROUTES from "../../constants/routes";

const styles = theme => ({
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  input: {
    display: "none"
  },
  spectrogram: {
    height: "350px",
    overflow: "hidden"
  },
  upload: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
  },
  fab: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  panel: {
    width: "100%",
    backgroundColor: theme.palette.secondary.light
  }
});

const INITIAL_STATE = {
  word: "",
  copy: "",
  description: "",
  dialectsArray: [],
  selectedLanguage: "",
  nonNative: {
    label: "Non Native",
    audio: "",
    image: "",
    message: "Upload Audio",
    open: false,
    waveform: createRef(),
    spectrogram: createRef(),
    wavesurfer: ""
  },
  disableDialectButton: false,
  availableDialects: [],
  audio: "",
  image: "",
  uploadMessageAudio: "Upload Audio",
  uploadMessageImage: "Upload Image",
  loading: false,

  uploading: {
    items: 0,
    uploadedItems: 0,
    open: false
  },
  languages: "",
  error: null
};

const dialectCell = (
  id,
  dialect,
  classes,
  onChange,
  onUploadAudio,
  onConfirmDialect,
  onDeleteDialect,
  language,
  availableDialects
) => {
  const dialectsList = availableDialects;

  const isInvalid = dialect.audio === "" || dialect.dialect === "";

  return (
    <React.Fragment key={`dialect-cell-${id}`}>
      <Grid item xs={12}>
        <ExpansionPanel className={classes.panel} expanded={dialect.open}>
          <ExpansionPanelSummary>
            <Typography>{dialect.message}</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <Grid container spacing={2}>
              <Grid item xs={6}>
                <InputLabel required id={`dialectLabel-${id}`}>
                  Dialect
                </InputLabel>
                <Select
                  native
                  value={dialect.dialect}
                  name="selectedDialect"
                  labelId={`dialectLabel-${id}`}
                  variant="outlined"
                  fullWidth
                  onChange={e => {
                    onChange(e, id);
                  }}
                >
                  <option value="" />
                  {dialectsList.map(d => (
                    <option key={d.dialectId} value={d.dialectId}>
                      {d.dialect}
                    </option>
                  ))}
                </Select>
              </Grid>
              <Grid item xs={6} className={classes.upload}>
                <input
                  accept="audio/wav"
                  className={classes.input}
                  id={`audioFile-${id}`}
                  onChange={e => {
                    onUploadAudio(e, id);
                  }}
                  type="file"
                />
                <label htmlFor={`audioFile-${id}`}>
                  <Button
                    color="secondary"
                    component="span"
                    fullWidth
                    variant="contained"
                  >
                    {dialect.message}
                  </Button>
                </label>
              </Grid>

              <Grid item xs={12}>
                <div id={`waveform-${id}`} ref={dialect.waveform}></div>
              </Grid>
              <Grid item xs={12}>
                <div
                  className={classes.spectrogram}
                  ref={dialect.spectrogram}
                  id={`spectrogram-${id}`}
                ></div>
              </Grid>
            </Grid>
          </ExpansionPanelDetails>
          <ExpansionPanelActions>
            <Button
              onClick={e => {
                onDeleteDialect(e, id);
              }}
              color="primary"
              component="span"
              variant="contained"
            >
              DELETE
            </Button>
            <Button
              color="secondary"
              component="span"
              variant="contained"
              disabled={isInvalid}
              onClick={e => {
                onConfirmDialect(e, id);
              }}
            >
              OK
            </Button>
          </ExpansionPanelActions>
        </ExpansionPanel>
      </Grid>
    </React.Fragment>
  );
};

class WordFormBase extends Component {
  constructor(props) {
    super(props);

    this.state = { ...INITIAL_STATE };

    this.audioEl = createRef();
  }

  componentDidMount() {
    this.props.firebase.languages().on("value", snapshot => {
      const languagesObject = snapshot.val();

      const languagesList = Object.keys(languagesObject).map(key => ({
        ...languagesObject[key],
        languageId: key
      }));

      this.setState({
        languages: languagesList,
        loading: false
      });
    });
  }

  componentWillUnmount() {
    // this.wavesurfer.un("ready", this.addSpectrogram);
    this.props.firebase.languages().off();
  }

  addDialect = event => {
    const emptyItem = {
      dialect: "",
      audio: "",
      image: "",
      open: true,
      waveform: createRef(),
      spectrogram: createRef(),
      wavesurfer: "",
      message: "Upload Audio"
    };

    this.setState(prevState => ({
      dialectsArray: [...prevState.dialectsArray, emptyItem],
      disableDialectButton: true
    }));
  };

  componentDidUpdate() {
    const { dialectsArray } = this.state;

    const id = dialectsArray.findIndex(d => d.open === true);
    if (id >= 0 && !dialectsArray[id].wavesurfer) {
      dialectsArray[id].wavesurfer = WaveSurfer.create({
        container: dialectsArray[id].waveform.current,
        waveColor: "violet",
        progressColor: "purple",
        plugins: [
          SpectrogramPlugin.create({
            container: dialectsArray[id].spectrogram.current,
            fftSamples: 2048,
            noverlap: 0,
            labels: false,
            colorMap: COLORMAPS.hsv
          })
        ]
      });
      this.setState({ dialectsArray });
    }
  }

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  onChangeLanguage = event => {
    const languageId = event.target.value;
    const language = this.state.languages.find(
      l => l.languageId === languageId
    );
    const dialectsList = Object.keys(language.dialects).map(key => ({
      ...language.dialects[key],
      dialectId: key
    }));

    this.setState({
      selectedLanguage: languageId,
      availableDialects: dialectsList,
      dialectsArray: []
    });
  };

  onUploadAudioNonNative = event => {
    const uploadedFile = event.target.files[0];

    let { nonNative } = this.state;

    nonNative.audio = uploadedFile;
    nonNative.message = uploadedFile.name;

    this.setState({ nonNative });

    const ws = nonNative.wavesurfer;
    ws.empty();
    ws.loadBlob(uploadedFile);
  };

  onUploadAudioDialect = (event, id) => {
    const uploadedFile = event.target.files[0];

    let { dialectsArray } = this.state;

    dialectsArray[id].audio = uploadedFile;
    dialectsArray[id].message = uploadedFile.name;

    this.setState({ dialectsArray });

    const ws = dialectsArray[id].wavesurfer;
    ws.empty();
    ws.loadBlob(uploadedFile);
  };

  onConfirmNonNative = event => {
    let { nonNative } = this.state;

    nonNative.open = false;

    const canvas = nonNative.spectrogram.current.getElementsByTagName(
      "canvas"
    )[0];

    canvas.toBlob(blob => {
      nonNative.image = blob;

      this.setState({
        nonNative
      });
    });
  };

  onConfirmDialect = (event, id) => {
    let { dialectsArray, availableDialects } = this.state;

    dialectsArray[id].open = false;

    availableDialects = availableDialects.filter(
      d => d.dialectId !== dialectsArray[id].dialect
    );

    // dialectsArray[id].image = new Image();
    const canvas = dialectsArray[id].spectrogram.current.getElementsByTagName(
      "canvas"
    )[0];

    // const imgSrc = canvas.toDataURL("image/png");
    // dialectsArray[id].image.src = imgSrc;

    canvas.toBlob(blob => {
      dialectsArray[id].image = blob;

      this.setState({
        dialectsArray,
        disableDialectButton: false,
        availableDialects
      });
    });

    // const copy = this.wavesurfer.exportImage("image/png", 1, "blob");
    // const copy = new Image();
    // const canvas = this.spectrogram.getElementsByTagName("canvas")[0];
    // const imgSrc = canvas.toDataURL("image/png");
    // copy.src = imgSrc;
    // this.setState({ copy });
    // console.log(this.spectrogram);
    // console.log("i listened to the event");

    // this.setState({
    //   dialectsArray,
    //   disableDialectButton: false,
    //   availableDialects
    // });
  };

  onDeleteDialect = (event, id) => {
    let { dialectsArray } = this.state;
    dialectsArray.pop();

    this.setState({
      dialectsArray,
      disableDialectButton: false
    });
  };

  onChangeDialect = (event, id) => {
    const dialect = event.target.value;

    let { dialectsArray } = this.state;

    dialectsArray[id].dialect = dialect;

    this.setState({ dialectsArray });
  };

  handleClose = (event, id) => {
    let { dialectsArray } = this.state;

    dialectsArray[id].open = false;

    this.setState({ dialectsArray });
  };

  onUploadAudio = event => {
    const uploadedFile = event.target.files[0];

    this.setState({
      audio: uploadedFile,
      uploadMessageAudio: uploadedFile.name
    });
  };

  onUploadImage = event => {
    const uploadedFile = event.target.files[0];

    this.setState({
      image: uploadedFile,
      uploadMessageImage: uploadedFile.name
    });
  };

  openNonNative = event => {
    let { nonNative } = this.state;

    nonNative.open = !nonNative.open;

    if (!nonNative.wavesurfer) {
      nonNative.wavesurfer = WaveSurfer.create({
        container: nonNative.waveform.current,
        waveColor: "violet",
        progressColor: "purple",
        plugins: [
          SpectrogramPlugin.create({
            container: nonNative.spectrogram.current,
            fftSamples: 2048,
            noverlap: 0,
            labels: false,
            colorMap: COLORMAPS.hsv
          })
        ]
      });
    }

    this.setState({ nonNative });
  };

  refresh = event => {
    window.location.reload(false);
  };

  onSubmit = event => {
    const {
      word,
      description,
      dialectsArray,
      selectedLanguage,
      nonNative
    } = this.state;

    const wordDialects = dialectsArray.map(d => d.dialect);

    this.setState(
      {
        uploading: {
          open: true,
          items: wordDialects.length * 2 + 2,
          uploadedItems: 0
        }
      },
      () => {
        this.props.firebase
          .words(selectedLanguage)
          .push({
            name: word,
            description: description,
            dialects: wordDialects
          })
          .then(word => {
            const wordId = word.key;

            const metadataAudio = {
              contentType: "audio/wav"
            };

            const metadataImage = {
              contentType: "image/png"
            };

            // Upload dialects image and audio
            dialectsArray.forEach(d => {
              this.props.firebase
                .wordAudio(selectedLanguage, wordId, d.dialect)
                .put(d.audio, metadataAudio)
                .then(() => {
                  this.setState(prevState => ({
                    uploading: {
                      open: true,
                      items: prevState.uploading.items,
                      uploadedItems: prevState.uploading.uploadedItems + 1
                    }
                  }));
                });

              this.props.firebase
                .wordImage(selectedLanguage, wordId, d.dialect)
                .put(d.image, metadataImage)
                .then(() => {
                  this.setState(prevState => ({
                    uploading: {
                      open: true,
                      items: prevState.uploading.items,
                      uploadedItems: prevState.uploading.uploadedItems + 1
                    }
                  }));
                });
            });

            // Upload Non Native image and audio
            this.props.firebase
              .wordAudio(selectedLanguage, wordId, "nonnative")
              .put(nonNative.audio, metadataAudio)
              .then(() => {
                this.setState(prevState => ({
                  uploading: {
                    open: true,
                    items: prevState.uploading.items,
                    uploadedItems: prevState.uploading.uploadedItems + 1
                  }
                }));
              });

            this.props.firebase
              .wordImage(selectedLanguage, wordId, "nonnative")
              .put(nonNative.image, metadataImage)
              .then(() => {
                this.setState(prevState => ({
                  uploading: {
                    open: true,
                    items: prevState.uploading.items,
                    uploadedItems: prevState.uploading.uploadedItems + 1
                  }
                }));
              });
          })
          .then(() => {
            // this.setState(prevState => ({
            //   ...INITIAL_STATE,
            //   language: prevState.language
            // }));
            // this.props.history.push(ROUTES.WORD);
          })
          .catch(error => {
            this.setState({ error });
          });
      }
    );

    event.preventDefault();
  };

  render() {
    const { classes } = this.props;

    const {
      dialectsArray,
      word,
      description,
      nonNative,
      languages,
      selectedLanguage,
      disableDialectButton,
      availableDialects,
      uploading,
      error
    } = this.state;

    const isInvalid =
      word === "" ||
      selectedLanguage === "" ||
      dialectsArray.length === 0 ||
      nonNative.image === "";

    const addDialectIsInvalid =
      selectedLanguage === "" ||
      disableDialectButton ||
      availableDialects.length === 0;

    const nonNativeIsInvalid = nonNative.audio === "";

    return (
      <form className={classes.form} noValidate onSubmit={this.onSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              autoFocus
              fullWidth
              id="word"
              label="Word"
              name="word"
              value={word}
              onChange={this.onChange}
            />
          </Grid>
          <Grid item xs={12}>
            <InputLabel required id="languageLabel">
              Language
            </InputLabel>
            <Select
              native
              value={selectedLanguage}
              name="selectedLanguage"
              labelId="languageLabel"
              variant="outlined"
              fullWidth
              onChange={this.onChangeLanguage}
            >
              <option value="" />
              {languages &&
                languages.map(language => (
                  <option key={language.languageId} value={language.languageId}>
                    {language.name}
                  </option>
                ))}
            </Select>
          </Grid>
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              fullWidth
              id="description"
              label="Description"
              name="description"
              value={description}
              onChange={this.onChange}
              multiline={true}
              rows={3}
              rowsMax={10}
            />
          </Grid>
          <Grid item xs={12}>
            <Fab
              className={classes.fab}
              onClick={this.addDialect}
              disabled={addDialectIsInvalid}
            >
              <AddIcon />
            </Fab>
          </Grid>
          {dialectsArray.length !== 0 &&
            dialectsArray.map((dialect, index) =>
              dialectCell(
                index,
                dialect,
                classes,
                this.onChangeDialect,
                this.onUploadAudioDialect,
                this.onConfirmDialect,
                this.onDeleteDialect,
                languages.find(lan => lan.languageId === selectedLanguage),
                availableDialects
              )
            )}
          {/* <Grid item xs={6}>
            <TextField
              variant="outlined"
              margin="normal"
              fullWidth
              disabled
              id="nonNative"
              label={nonNative.label}
              name="nonNative"
            />
          </Grid>
          <Grid item xs={6} className={classes.upload}>
            <input
              accept="audio/wav"
              className={classes.input}
              id="nonNativeFile"
              onChange={this.onUploadAudioNonNative}
              type="file"
            />
            <label htmlFor="nonNativeFile">
              <Button
                color="secondary"
                component="span"
                fullWidth
                variant="contained"
              >
                {nonNative.message}
              </Button>
            </label>
          </Grid> */}
          <Grid item xs={12}>
            <ExpansionPanel
              className={classes.panel}
              onChange={this.openNonNative}
              expanded={nonNative.open}
            >
              <ExpansionPanelSummary
                expandIcon={nonNative.image === "" ? <AddIcon /> : null}
              >
                <Typography>Non Native</Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <Grid container spacing={2}>
                  <Grid item xs={6}>
                    <TextField
                      variant="outlined"
                      margin="normal"
                      fullWidth
                      disabled
                      id="nonNative"
                      label={nonNative.label}
                      name="nonNative"
                    />
                  </Grid>
                  <Grid item xs={6} className={classes.upload}>
                    <input
                      accept="audio/wav"
                      className={classes.input}
                      id="audioFileNonNative"
                      onChange={this.onUploadAudioNonNative}
                      type="file"
                    />
                    <label htmlFor="audioFileNonNative">
                      <Button
                        color="secondary"
                        component="span"
                        fullWidth
                        variant="contained"
                      >
                        {nonNative.message}
                      </Button>
                    </label>
                  </Grid>

                  <Grid item xs={12}>
                    <div id="waveform-nonnative" ref={nonNative.waveform}></div>
                  </Grid>
                  <Grid item xs={12}>
                    <div
                      className={classes.spectrogram}
                      ref={nonNative.spectrogram}
                      id="spectrogram-nonnative"
                    ></div>
                  </Grid>
                </Grid>
              </ExpansionPanelDetails>
              <ExpansionPanelActions>
                <Button
                  color="secondary"
                  component="span"
                  variant="contained"
                  disabled={nonNativeIsInvalid}
                  onClick={this.onConfirmNonNative}
                >
                  OK
                </Button>
              </ExpansionPanelActions>
            </ExpansionPanel>
          </Grid>
          <Grid item xs={12}>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              disabled={isInvalid}
            >
              Add
            </Button>
            {error && <Typography color="error">{error.message}</Typography>}
          </Grid>
        </Grid>
        <Dialog open={uploading.open}>
          <DialogTitle>
            <Typography color="error">Please Wait</Typography>
          </DialogTitle>
          <DialogContent>
            <DialogContentText>
              {uploading.uploadedItems}/{uploading.items}
            </DialogContentText>
            <LinearProgress
              value={(uploading.uploadedItems / uploading.items) * 100}
            />
            <Button
              disabled={uploading.uploadedItems !== uploading.items}
              onClick={this.refresh}
            >
              REFRESH
            </Button>
          </DialogContent>
        </Dialog>
      </form>
    );
  }
}

const WordForm = compose(
  withRouter,
  withFirebase,
  withStyles(styles)
)(WordFormBase);

export default WordForm;
