import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";

import LogInPage from "../LogIn";
import PasswordForgetPage from "../PasswordForget";
import SignUpPage from "../SignUp";
import TeacherPortalPage from "../TeacherPortal";
import StudentPage from "../StudentPortal";
import Homework from "../Homework";
import WordPage from "../Word";
import Test from "../Test";

import * as ROUTES from "../../constants/routes";
import Dashboard from "../Dashboard";
import { withAuthentication } from "../Session";
import LoadingPage from "../Loading";
import WordListPage from "../WordList";
import WordPracticePage from "../WordPractice";
import ClassPage from "../Class";
import StudentHomeworkPage from "../StudentHomework";
import HomeworkCardPage from "../HomeworkInfo";
import GradingPage from "../Grading";
import LanguagePage from "../Language";

class App extends Component {
  render() {
    return (
      <Router>
        <div>
          <Dashboard />
        </div>
        <Route exact path={ROUTES.LANDING} component={LogInPage} />
        <Route path={ROUTES.PASSWORD_FORGET} component={PasswordForgetPage} />
        <Route path={ROUTES.SIGN_UP} component={SignUpPage} />
        <Route path={ROUTES.TEACHER_PORTAL} component={TeacherPortalPage} />
        <Route path={ROUTES.CLASS} component={ClassPage} />
        <Route path={ROUTES.HOMEWORK} component={Homework} />
        <Route path={ROUTES.WORD} component={WordPage} />
        <Route path={ROUTES.GRADING} component={GradingPage} />
        <Route path={ROUTES.LANGUAGE} component={LanguagePage} />
        <Route path={ROUTES.WORD_LIST} component={WordListPage} />
        <Route path={ROUTES.AUDIOTEST} component={Test} />
        <Route path={ROUTES.LOADING} component={LoadingPage} />
        <Route path={ROUTES.STUDENT_PORTAL} component={StudentPage} />
        <Route
          path={ROUTES.STUDENT_HOMEWORKS}
          component={StudentHomeworkPage}
        />
        <Route
          path={`${ROUTES.PRACTICE}/:languageId/:wordId`}
          component={WordPracticePage}
        />
        <Route
          path={`${ROUTES.HOMEWORK_INFO}/:classId/:assignmentId`}
          component={HomeworkCardPage}
        />
      </Router>
    );
  }
}

export default withAuthentication(App);
