import React, { Component } from "react";
import { compose } from "recompose";
import { withRouter } from "react-router-dom";
import { withFirebase } from "../Firebase";
import { withStyles } from "@material-ui/styles";
import {
  Grid,
  TextField,
  Button,
  Select,
  InputLabel,
  Typography
} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker
} from "@material-ui/pickers";

import * as STATUS from "../../constants/assignmentstatus";

const styles = theme => ({
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  }
});

const INITIAL_STATE = {
  title: "",
  description: "",
  _classes: [],
  words: [],
  selectedWords: [],
  selectedClass: "",
  selectedDate: new Date(),
  error: null
};

class HomeworkFormBase extends Component {
  constructor(props) {
    super(props);

    this.state = { ...INITIAL_STATE, loading: false };
  }

  componentDidMount() {
    this.setState({ loading: true });

    this.unsubscribe = this.props.firebase.auth.onAuthStateChanged(currUser => {
      this.props.firebase.classes().on("value", snapshot => {
        const classesObject = snapshot.val();

        const classesList = Object.keys(classesObject)
          .map(key => ({
            ...classesObject[key],
            classId: key
          }))
          .filter(_class => _class.teacher === currUser.uid);
        console.log(classesList);
        this.setState({
          _classes: classesList
          // loading: false
        });

        // this.props.firebase.words().on("value", snapshot => {
        //   const wordsObject = snapshot.val();

        //   const wordsList = Object.keys(wordsObject).map(key => ({
        //     ...wordsObject[key],
        //     wordId: key
        //   }));

        //   this.setState({
        //     words: wordsList,
        //     loading: false
        //   });
        // });
      });
    });
  }

  componentWillUnmount() {
    this.props.firebase.classes().off();
    this.unsubscribe();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const { _classes, selectedClass } = this.state;
    if (selectedClass !== prevState.selectedClass && selectedClass !== "") {
      this.setState({ loading: true });
      const languageId = _classes.find(
        _class => _class.classId === selectedClass
      ).languageId;

      this.props.firebase.words(languageId).on("value", snapshot => {
        const wordsObject = snapshot.val();
        const wordsList = Object.keys(wordsObject).map(key => ({
          ...wordsObject[key],
          wordId: key
        }));
        this.setState({
          words: wordsList,
          loading: false
        });
      });
    }
  }

  onSubmit = event => {
    const {
      title,
      description,
      selectedClass,
      selectedWords,
      selectedDate,
      _classes,
      words
    } = this.state;

    const wordsId = selectedWords.map(word => word.wordId);
    const date = selectedDate.toString();

    this.props.firebase
      .classAssignments(selectedClass)
      .push({
        title,
        description,
        words: wordsId,
        date
      })
      .then(assignment => {
        const assignmentId = assignment.key;
        console.log(assignment);
        this.props.firebase
          .classStudents(selectedClass)
          .on("value", snapshot => {
            const studentsList = snapshot.val();

            studentsList.forEach(studentId => {
              this.props.firebase
                .userAssignments(studentId)
                .child(assignmentId)
                .set({
                  title,
                  classId: selectedClass,
                  status: STATUS.NEW,
                  date
                });
            });
          });
      })
      .then(() => {
        this.setState({ ...INITIAL_STATE });
        this.setState({ _classes, words });
      })
      .catch(error => {
        this.setState({ error });
      });

    event.preventDefault();
  };

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  onDateChange = date => {
    this.setState({ selectedDate: date });
  };

  onChangeAutocomplete = (event, value) => {
    this.setState({ selectedWords: value });
  };

  render() {
    const { classes } = this.props;

    const {
      title,
      description,
      _classes,
      selectedClass,
      words,
      selectedWords,
      selectedDate,
      loading,
      error
    } = this.state;

    const isInvalid =
      title === "" || selectedClass === "" || selectedWords.length === 0;

    return (
      <form className={classes.form} noValidate onSubmit={this.onSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              autoFocus
              fullWidth
              id="title"
              label="Title"
              name="title"
              value={title}
              onChange={this.onChange}
            />
          </Grid>
          <Grid item xs={12}>
            <InputLabel required id="classLabel">
              Class
            </InputLabel>
            <Select
              native
              value={selectedClass}
              name="selectedClass"
              labelId="classLabel"
              variant="outlined"
              fullWidth
              onChange={this.onChange}
            >
              <option value="" />
              {_classes.map(_class => (
                <option key={_class.classId} value={_class.classId}>
                  {_class.title}
                </option>
              ))}
            </Select>
          </Grid>
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              fullWidth
              id="description"
              label="Description"
              name="description"
              value={description}
              onChange={this.onChange}
              multiline={true}
              rows={3}
              rowsMax={10}
            />
          </Grid>
          <Grid item xs={12}>
            <Autocomplete
              disabled={selectedClass === ""}
              multiple
              value={selectedWords}
              options={words}
              getOptionLabel={option => option.name}
              filterSelectedOptions
              loading={loading}
              onChange={this.onChangeAutocomplete}
              renderInput={params => (
                <TextField
                  {...params}
                  variant="outlined"
                  margin="normal"
                  label="Words"
                  fullWidth
                />
              )}
            />
          </Grid>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <Grid item xs={12} sm={6}>
              <KeyboardDatePicker
                disableToolbar
                variant="outlined"
                format="MM/dd/yyyy"
                margin="normal"
                id="due-date"
                label="Due Date"
                value={selectedDate}
                onChange={this.onDateChange}
                KeyboardButtonProps={{
                  "aria-label": "change date"
                }}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <KeyboardTimePicker
                variant="outlined"
                margin="normal"
                id="due-time"
                label="Due Time"
                value={selectedDate}
                onChange={this.onDateChange}
                KeyboardButtonProps={{
                  "aria-label": "change time"
                }}
              />
            </Grid>
          </MuiPickersUtilsProvider>
          <Grid item xs={12}>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              disabled={isInvalid}
            >
              Add
            </Button>
            {error && <Typography color="error">{error.message}</Typography>}
          </Grid>
        </Grid>
      </form>
    );
  }
}

const HomeworkForm = compose(
  withRouter,
  withFirebase,
  withStyles(styles)
)(HomeworkFormBase);

export default HomeworkForm;
