import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import {
  Grid,
  Typography,
  TextField,
  Button,
  Link as MaterialLink,
  Select,
  MenuItem,
  InputLabel,
  FormControl
} from "@material-ui/core";
import { withStyles } from "@material-ui/styles";
import { compose } from "recompose";

import { withFirebase } from "../Firebase";

import * as ROUTES from "../../constants/routes";
import * as ROLES from "../../constants/roles";

const styles = theme => ({
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  }
});

const INITIAL_STATE = {
  first: "",
  last: "",
  email: "",
  passwordOne: "",
  passwordTwo: "",
  role: "",
  error: null
};

class SignUpFormBase extends Component {
  constructor(props) {
    super(props);

    this.state = { ...INITIAL_STATE };
  }

  onSubmit = event => {
    const { first, last, email, passwordOne, role } = this.state;

    this.props.firebase
      .doCreateUserWithEmailAndPassword(email, passwordOne)
      .then(authUser => {
        // Create a user in your Firebase realtime database
        return this.props.firebase.user(authUser.user.uid).set({
          first,
          last,
          role,
          email
        });
      })
      .then(() => {
        this.setState({ ...INITIAL_STATE });
        this.props.history.push(ROUTES.LOADING);
      })
      .catch(error => {
        this.setState({ error });
      });
    event.preventDefault();
  };

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  render() {
    const { classes } = this.props;

    const {
      first,
      last,
      email,
      passwordOne,
      passwordTwo,
      role,
      error
    } = this.state;

    const isInvalid =
      passwordOne !== passwordTwo ||
      passwordOne === "" ||
      email === "" ||
      first === "" ||
      last === "" ||
      role === "";

    return (
      <form className={classes.form} noValidate onSubmit={this.onSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              id="first"
              label="First Name"
              name="first"
              value={first}
              onChange={this.onChange}
              autoComplete="given-name"
              fullWidth
              autoFocus
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              id="last"
              label="Last Name"
              name="last"
              value={last}
              onChange={this.onChange}
              autoComplete="family-name"
              fullWidth
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              value={email}
              onChange={this.onChange}
              type="email"
              autoComplete="email"
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="passwordOne"
              label="Password"
              name="passwordOne"
              value={passwordOne}
              onChange={this.onChange}
              type="password"
              autoComplete="password"
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="passwordTwo"
              label="Confirm Password"
              name="passwordTwo"
              value={passwordTwo}
              onChange={this.onChange}
              type="password"
              autoComplete="password"
            />
          </Grid>
          <Grid item xs={12}>
            <FormControl required fullWidth margin="normal">
              <InputLabel>Who are you?</InputLabel>
              <Select
                id="role"
                name="role"
                value={role}
                onChange={this.onChange}
              >
                <MenuItem value={""}>None</MenuItem>
                <MenuItem value={ROLES.TEACHER}>Teacher</MenuItem>
                <MenuItem value={ROLES.STUDENT}>Student</MenuItem>
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12}>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              disabled={isInvalid}
            >
              Sign Up
            </Button>
          </Grid>
        </Grid>
        <Grid container alignItems="flex-end" alignContent="flex-end">
          {error && <Typography color="error">{error.message}</Typography>}
          <Grid item xs={12} sm={6}>
            <MaterialLink component={Link} to={ROUTES.PASSWORD_FORGET}>
              <Typography variant="body2">Forgot password?</Typography>
            </MaterialLink>
          </Grid>
          <Grid item xs={12} sm={6}>
            <MaterialLink component={Link} to={ROUTES.LANDING}>
              <Typography variant="body2">
                Already have an account? Sign In
              </Typography>
            </MaterialLink>
          </Grid>
        </Grid>
      </form>
    );
  }
}

const SignUpForm = compose(
  withRouter,
  withFirebase,
  withStyles(styles)
)(SignUpFormBase);

export default SignUpForm;
