import React, { Component } from "react";
import { compose } from "recompose";
import { Link } from "react-router-dom";

import { withFirebase } from "../Firebase";
import {
  Grid,
  List,
  ListItemText,
  ListItem,
  InputLabel,
  Select,
  Button,
  Container
} from "@material-ui/core";
import CircularProgress from "@material-ui/core/CircularProgress";
import { withStyles } from "@material-ui/styles";

import * as ROUTES from "../../constants/routes";

const styles = theme => ({
  root: {
    height: "100vh"
  },
  goButton: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
  }
});

class WordListBase extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      languages: [],
      selectedLanguage: "",
      words: []
    };
  }

  componentDidMount() {
    this.props.firebase.languages().on("value", snapshot => {
      const languagesObject = snapshot.val();

      const languagesList = Object.keys(languagesObject).map(key => ({
        ...languagesObject[key],
        languageId: key
      }));

      this.setState({
        languages: languagesList
      });
    });
  }

  componentWillUnmount() {
    this.props.firebase.languages().off();
  }

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  onChangeLanguage = event => {
    const selectedLanguage = event.target.value;

    this.setState({ selectedLanguage, words: [] });

    if (selectedLanguage === "") {
      return;
    }

    this.props.firebase.words(selectedLanguage).once("value", snapshot => {
      const wordsObject = snapshot.val();

      const wordsList = Object.keys(wordsObject).map(key => ({
        ...wordsObject[key],
        wordId: key
      }));

      this.setState({ words: wordsList });
    });
  };

  render() {
    const { classes } = this.props;

    const { words, languages, selectedLanguage, loading } = this.state;

    return (
      <Container component="main" maxWidth="sm" className={classes.root}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <InputLabel required id="languageLabel">
              Language
            </InputLabel>
            <Select
              native
              value={selectedLanguage}
              name="selectedLanguage"
              labelId="languageLabel"
              variant="outlined"
              fullWidth
              onChange={this.onChangeLanguage}
            >
              <option value="" />
              {languages &&
                languages.map(language => (
                  <option key={language.languageId} value={language.languageId}>
                    {language.name}
                  </option>
                ))}
            </Select>
          </Grid>

          <List className={classes.list}>
            {selectedLanguage !== "" &&
              words.map(word => (
                <ListItem
                  button={true}
                  component={Link}
                  to={`${ROUTES.PRACTICE}/${selectedLanguage}/${word.wordId}`}
                  key={word.wordId}
                >
                  <ListItemText primary={word.name} />
                </ListItem>
              ))}
          </List>
        </Grid>
      </Container>
    );
  }
}

const WordList = compose(withFirebase, withStyles(styles))(WordListBase);

export default WordList;
