import React, { Component, createRef } from "react";
import { compose } from "recompose";
import { withStyles } from "@material-ui/styles";
import { withFirebase } from "../Firebase";
import {
  Typography,
  Grid,
  CardMedia,
  Card,
  InputLabel,
  Select,
  Fab,
  Tooltip
} from "@material-ui/core";
import CircularProgress from "@material-ui/core/CircularProgress";
import Spectrogram from "../Spectrogram";
import FiberManualRecordIcon from "@material-ui/icons/FiberManualRecord";
import RecorderJS from "recorder-js";
import StopIcon from "@material-ui/icons/Stop";
import PlayArrowIcon from "@material-ui/icons/PlayArrow";
import WaveSurfer from "wavesurfer.js";
import SpectrogramPlugin from "wavesurfer.js/dist/plugin/wavesurfer.spectrogram.min.js";

import * as ROUTES from "../../constants/routes";
import * as COLORMAPS from "../../constants/colormaps";
import { getAudioStream, exportBuffer } from "../../utilities/audio";
import Recorder from "../Recorder";

import "../../index.css";

const styles = theme => ({
  root: {
    height: "100vh"
  },
  card: {
    height: "300px",
    width: "100px"
  },
  goButton: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
  },
  spectrogramContainer: {
    height: "350px",
    // width: "100%",
    overflowY: "hidden",
    overflowX: "scroll",
    borderRadius: "25px",
    position: "relative"
  },
  spectrogram: {
    marginTop: "-674px"
  },
  native: {
    backgroundColor: "#C2AFF0",
    borderRadius: "25px"
  },
  userInput: {
    backgroundColor: "#9191E9",
    borderRadius: "25px"
  },
  nonNative: {
    backgroundColor: "#457EAC",
    borderRadius: "25px"
  },
  recordingButton: {
    position: "absolute",
    color: theme.palette.warning.main,
    bottom: "1%",
    left: "10%",
    zIndex: "5"
  },
  playButton: {
    position: "absolute",
    color: theme.palette.success.main,
    bottom: "1%",
    left: "1%",
    zIndex: "5"
  }
});

const INITIAL_STATE = {
  loading: false,
  word: {},
  dialectData: {},
  nonNativeData: {
    imageUrl: "",
    audioUrl: "",
    audioEl: ""
  },
  selectedDialect: "",
  dialects: {},
  stream: null,
  recording: false,
  recorder: null,
  error: "",
  userInput: {
    waveform: createRef(),
    spectrogram: createRef(),
    wavesurfer: "",
    audioEl: ""
  }
};

class PracticeFragmentBase extends Component {
  constructor(props) {
    super(props);

    this.state = {
      ...INITIAL_STATE
    };
  }

  async componentDidMount() {
    this.setState({ loading: true });

    const { languageId, wordId } = this.props;

    let { nonNativeData } = this.state;

    this.props.firebase
      .language(languageId)
      .once("value", snapshot => {
        const dialects = snapshot.val().dialects;
        this.setState({ dialects });
      })
      .then(() => {
        this.props.firebase.word(languageId, wordId).once("value", snapshot => {
          const wordObject = snapshot.val();

          let { dialectData } = this.state;

          wordObject.dialects.forEach(
            dialectId =>
              (dialectData[dialectId] = {
                audioUrl: "",
                imageUrl: "",
                audioEl: ""
              })
          );
          this.setState({
            word: wordObject,
            dialectData,
            loading: false
          });
        });
      });

    this.props.firebase
      .wordAudio(languageId, wordId, "nonnative")
      .getDownloadURL()
      .then(url => {
        nonNativeData.audioUrl = url;
        nonNativeData.audioEl = new Audio(url);
        this.setState({ nonNativeData });
      });

    this.props.firebase
      .wordImage(languageId, wordId, "nonnative")
      .getDownloadURL()
      .then(url => {
        nonNativeData.imageUrl = url;
        this.setState({ nonNativeData });
      });

    let stream;

    try {
      // We will implement this later.
      stream = await getAudioStream();
    } catch (error) {
      // Users browser doesn't support audio.
      // Add your handler here.
      this.setState({ error });
    }

    this.setState({ stream });

    let { userInput } = this.state;

    userInput.wavesurfer = WaveSurfer.create({
      container: userInput.waveform.current,
      waveColor: "violet",
      progressColor: "purple",
      plugins: [
        SpectrogramPlugin.create({
          container: userInput.spectrogram.current,
          fftSamples: 2048,
          noverlap: 0,
          labels: false,
          colorMap: COLORMAPS.hsv
        })
      ]
    });
  }

  componentWillUnmount() {
    const { wordId } = this.props;

    this.props.firebase.word(wordId).off();
  }

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  onChangeDialect = event => {
    const selectedDialect = event.target.value;

    this.setState({ selectedDialect });

    let { dialectData } = this.state;
    const { languageId, wordId } = this.props;

    if (selectedDialect === "") {
      return;
    }

    if (dialectData[selectedDialect].audioUrl === "") {
      this.props.firebase
        .wordAudio(languageId, wordId, selectedDialect)
        .getDownloadURL()
        .then(url => {
          dialectData[selectedDialect].audioUrl = url;
          dialectData[selectedDialect].audioEl = new Audio(url);
          this.setState({ dialectData });
        });
    }

    if (dialectData[selectedDialect].imageUrl === "") {
      this.props.firebase
        .wordImage(languageId, wordId, selectedDialect)
        .getDownloadURL()
        .then(url => {
          dialectData[selectedDialect].imageUrl = url;
          this.setState({ dialectData });
        });
    }
  };

  handleRecordingButton = event => {
    const { recording } = this.state;

    if (recording) {
      this.stopRecord();
    } else {
      this.startRecord();
    }
  };

  startRecord() {
    const { stream } = this.state;

    const audioContext = new (window.AudioContext ||
      window.webkitAudioContext)();
    const recorder = new RecorderJS(audioContext);
    recorder.init(stream);

    this.setState(
      {
        recorder,
        recording: true
      },
      () => {
        recorder.start();
      }
    );
  }

  async stopRecord() {
    const { recorder, userInput } = this.state;

    const { blob, buffer } = await recorder.stop();
    //const audio = exportBuffer(buffer[0]);
    console.log(blob);
    // Do your audio processing here.
    //console.log(audio);

    const ws = userInput.wavesurfer;
    ws.empty();
    ws.loadBlob(blob);

    userInput.audioEl = new Audio(URL.createObjectURL(blob));

    this.setState({
      recording: false,
      userInput
    });
  }

  playUserInput = event => {
    const { userInput } = this.state;

    userInput.audioEl.play();
  };

  playDialect = event => {
    const { dialectData, selectedDialect } = this.state;

    dialectData[selectedDialect].audioEl.play();
  };

  playNonNative = event => {
    const { nonNativeData } = this.state;

    nonNativeData.audioEl.play();
  };

  render() {
    const {
      loading,
      word,
      downloaded,
      audioUrl,
      imageUrl,
      selectedDialect,
      dialects,
      dialectData,
      nonNativeData,
      userInput,
      recording
    } = this.state;

    const { classes } = this.props;

    const isInvalid = selectedDialect === "";

    return (
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Typography
            align="center"
            component="h1"
            variant="h4"
            color="primary"
          >
            {word.name}
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant="body1">{word.description}</Typography>
        </Grid>
        <Grid item xs={12}>
          <InputLabel required id="dialectLabel">
            Dialect
          </InputLabel>
          <Select
            native
            value={selectedDialect}
            name="selectedDialect"
            labelId="dialectLabel"
            variant="outlined"
            fullWidth
            onChange={this.onChangeDialect}
          >
            <option value="" />
            {word.dialects &&
              word.dialects.map(dialectId => (
                <option key={dialectId} value={dialectId}>
                  {dialects[dialectId].dialect}
                </option>
              ))}
          </Select>
        </Grid>

        <Grid item className={classes.native} xs={12}>
          <div className={classes.spectrogramContainer}>
            {selectedDialect !== "" &&
              dialectData[selectedDialect].imageUrl !== "" && (
                <Tooltip
                  disableTouchListener
                  disableFocusListener
                  title={`NATIVE: ${dialects[selectedDialect].dialect}`}
                >
                  <img
                    src={dialectData[selectedDialect].imageUrl}
                    className={classes.spectrogram}
                  />
                </Tooltip>
              )}
            <Fab
              size="small"
              className={classes.playButton}
              onClick={this.playDialect}
              disabled={
                selectedDialect === "" ||
                !dialectData[selectedDialect] ||
                dialectData[selectedDialect].audioEl === ""
              }
            >
              <PlayArrowIcon />
            </Fab>
          </div>
        </Grid>

        <Grid item className={classes.userInput} xs={12}>
          <div
            className={classes.spectrogramContainer}
            ref={userInput.spectrogram}
            id="spectrogram"
          >
            <Fab
              size="small"
              onClick={this.handleRecordingButton}
              className={classes.recordingButton}
            >
              {recording ? <StopIcon /> : <FiberManualRecordIcon />}
            </Fab>
            <Fab
              size="small"
              className={classes.playButton}
              onClick={this.playUserInput}
              disabled={userInput.audioEl === ""}
            >
              <PlayArrowIcon />
            </Fab>
          </div>
        </Grid>

        <Grid item className={classes.nonNative} xs={12}>
          <div className={classes.spectrogramContainer}>
            {selectedDialect !== "" && nonNativeData.imageUrl !== "" && (
              <Tooltip
                disableTouchListener
                disableFocusListener
                title="NON NATIVE"
              >
                <img
                  src={nonNativeData.imageUrl}
                  className={classes.spectrogram}
                />
              </Tooltip>
            )}
            <Fab
              size="small"
              className={classes.playButton}
              onClick={this.playNonNative}
              disabled={selectedDialect === "" || nonNativeData.audioEl === ""}
            >
              <PlayArrowIcon />
            </Fab>
          </div>
        </Grid>

        <Grid item xs={12}>
          <div id="waveform" ref={userInput.waveform}></div>
        </Grid>
      </Grid>
    );
  }
}

const PracticeFragment = compose(
  withFirebase,
  withStyles(styles)
)(PracticeFragmentBase);

export default PracticeFragment;
