import React, { Component } from "react";
import {
  Container,
  CssBaseline,
  Grid,
  InputLabel,
  Select,
  Button,
  ExpansionPanel,
  ExpansionPanelSummary,
  Typography,
  ExpansionPanelDetails,
  List,
  ListItem
} from "@material-ui/core";
import { compose } from "recompose";
import { withStyles } from "@material-ui/styles";
import { withFirebase } from "../Firebase";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

const styles = theme => ({
  root: {
    height: "100vh"
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  form: {
    margin: theme.spacing(4, 4)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  panel: {
    width: "100%"
  }
});

const INITIAL_STATE = {
  _classes: [],
  selectedClass: "",
  students: [],
  isDisabled: true,
  error: null
};

const generateStudents = (classes, students) => {
  return students.map((student, index) => (
    <ExpansionPanel key={`panel${index}`} className={classes.panel}>
      <ExpansionPanelSummary
        key={`summary${index}`}
        expandIcon={<ExpandMoreIcon />}
      >
        <Typography>
          {student.first} {student.last}
        </Typography>
      </ExpansionPanelSummary>
      <ExpansionPanelDetails key={`details${index}`}>
        <List>
          {student.assignmentsList.map((assignment, index2) => (
            <ListItem key={`item${index}-${index2}`}>
              <Grid container spacing={2}>
                <Grid item xs={8}>
                  {assignment.title}
                </Grid>
                <Grid item xs={4}>
                  {assignment.status}
                </Grid>
              </Grid>
            </ListItem>
          ))}
        </List>
      </ExpansionPanelDetails>
    </ExpansionPanel>
  ));
};

class GradingPageBase extends Component {
  constructor(props) {
    super(props);
    this.state = { ...INITIAL_STATE, loading: false };
  }

  componentDidMount() {
    this.setState({ loading: true });

    this.unsubscribe = this.props.firebase.auth.onAuthStateChanged(currUser => {
      this.props.firebase.userClasses(currUser.uid).on("value", snapshot => {
        const classesObject = snapshot.val();

        const classesList = Object.keys(classesObject).map(key => ({
          ...classesObject[key],
          classId: key
        }));

        this.setState({
          _classes: classesList,
          loading: false,
          uid: currUser.uid
        });
      });
    });
  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  onSubmit = event => {
    this.state.students.length = 0;

    const { selectedClass } = this.state;

    this.props.firebase.classStudents(selectedClass).on("value", snapshot => {
      const studentsList = snapshot.val();
      console.log(studentsList);

      studentsList.forEach(studentId => {
        this.props.firebase.user(studentId).on("value", snapshot => {
          const studentObject = snapshot.val();
          const studentKey = snapshot.key;

          const assignmentsObject = studentObject.assignments;

          const assignmentsList = Object.keys(assignmentsObject)
            .map(key => ({
              ...assignmentsObject[key],
              assignmentId: key
            }))
            .filter(assignment => assignment.classId === selectedClass);

          const student = {
            uid: studentKey,
            first: studentObject.first,
            last: studentObject.last,
            assignmentsList
          };

          this.setState(state => ({ students: [...state.students, student] }));
        });
      });
    });

    this.setState({ isDisabled: false });
    event.preventDefault();
  };

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  onChangeClass = event => {
    const selectedClass = event.target.value;

    this.setState({ selectedClass, students: [] });

    if (selectedClass === "") {
      return;
    }

    this.props.firebase.classStudents(selectedClass).on("value", snapshot => {
      const studentsList = snapshot.val();

      if (studentsList.length === 0) {
        return;
      }

      studentsList.forEach(studentId => {
        this.props.firebase.user(studentId).on("value", snapshot => {
          const studentObject = snapshot.val();
          const studentKey = snapshot.key;

          const assignmentsObject = studentObject.assignments;

          const assignmentsList = Object.keys(assignmentsObject)
            .map(key => ({
              ...assignmentsObject[key],
              assignmentId: key
            }))
            .filter(assignment => assignment.classId === selectedClass);

          const student = {
            uid: studentKey,
            first: studentObject.first,
            last: studentObject.last,
            assignmentsList
          };

          this.setState(state => ({ students: [...state.students, student] }));
        });
      });
    });
  };

  render() {
    const { classes } = this.props;

    const { selectedClass, _classes, students } = this.state;

    const isSelected = !selectedClass;

    return (
      <Container component="main" maxWidth="sm" className={classes.root}>
        <CssBaseline />
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <InputLabel id="classLabel">Class</InputLabel>
            <Select
              native
              value={selectedClass}
              name="selectedClass"
              labelId="classLabel"
              variant="outlined"
              fullWidth
              onChange={this.onChangeClass}
            >
              <option value="" />
              {_classes.map(_class => (
                <option key={_class.classId} value={_class.classId}>
                  {_class.title}
                </option>
              ))}
            </Select>
          </Grid>
        </Grid>
        <div className={classes.paper}>
          {generateStudents(classes, students)}
        </div>
      </Container>
    );
  }
}

const GradingPage = compose(withFirebase, withStyles(styles))(GradingPageBase);

export default GradingPage;
