import React, { Component } from "react";
import { compose } from "recompose";
import {
  Container,
  CssBaseline,
  Grid,
  Typography,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
  CardMedia
} from "@material-ui/core";
import { withStyles } from "@material-ui/styles";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

import { withFirebase } from "../Firebase";
import { withAuthorization } from "../Session";
import * as ROLES from "../../constants/roles";

const styles = theme => ({
  root: {
    height: "100vh"
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  panel: {
    width: "100%"
  },
  card: {
    height: "300px",
    width: "100px"
  },
  title: {
    color: theme.palette.secondary.main,
    textAlign: "center"
  }
});

const INITIAL_STATE = {
  title: "",
  description: "",
  words: [],
  date: "",
  loaded: false,
  error: null
};

const generateWords = (words, classes) => {
  return words.map((word, index) => (
    <ExpansionPanel key={`panel${index}`} className={classes.panel}>
      <ExpansionPanelSummary
        key={`summary${index}`}
        expandIcon={<ExpandMoreIcon />}
      >
        <Typography>{word.name}</Typography>
      </ExpansionPanelSummary>
      <ExpansionPanelDetails key={`detail${index}`}>
        <Grid container spacing={2}>
          <Grid item xs={12} align="center">
            <audio controls>
              <source src={word.audioUrl} type="audio/wav" />
              Your browser does not support the audio element.
            </audio>
          </Grid>
          <Grid item xs={12} align="center">
            <CardMedia
              component="img"
              className={classes.card}
              image={word.imageUrl}
            />
          </Grid>
        </Grid>
      </ExpansionPanelDetails>
    </ExpansionPanel>
  ));
};

class HomeworkCardPageBase extends Component {
  constructor(props) {
    super(props);

    this.state = { ...INITIAL_STATE };
  }

  componentDidMount() {
    const { classId, assignmentId } = this.props.match.params;

    this.props.firebase
      .classAssignment(classId, assignmentId)
      .once("value", snapshot => {
        const AssignmentObject = snapshot.val();

        const { title, description, words, date } = AssignmentObject;

        this.setState({ title, description, date });

        words.forEach(wordId => {
          this.props.firebase.word(wordId).once("value", snapshot => {
            const wordName = snapshot.val().name;
            const wordId = snapshot.key;
            this.props.firebase
              .wordAudio(wordId)
              .getDownloadURL()
              .then(url => {
                const audioUrl = url;

                this.props.firebase
                  .wordImage(wordId)
                  .getDownloadURL()
                  .then(url => {
                    const imageUrl = url;

                    const newWord = {
                      name: wordName,
                      wordId,
                      audioUrl,
                      imageUrl
                    };

                    this.setState(state => ({
                      words: [...state.words, newWord],
                      loaded: true
                    }));
                  });
              });
          });
        });
      });
  }

  render() {
    const { classes } = this.props;

    const { title, description, words, date, loaded } = this.state;

    const dateObject = new Date(date);

    return (
      <Container component="main" maxWidth="sm" className={classes.root}>
        <CssBaseline />
        <div className={classes.paper}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Typography className={classes.title} component="h1" variant="h5">
                {title}
              </Typography>
            </Grid>
            {loaded && (
              <Grid item xs={12}>
                <Typography>
                  Due Date: {dateObject.toDateString()} at{" "}
                  {dateObject.getHours()}:{dateObject.getMinutes()}
                </Typography>
              </Grid>
            )}

            <Grid item xs={12}>
              <Typography>{description}</Typography>
            </Grid>
            <Grid item xs={12}>
              {generateWords(words, classes)}
            </Grid>
          </Grid>
        </div>
      </Container>
    );
  }
}

const condition = authUser => !!authUser && authUser.role === ROLES.STUDENT;

const HomeworkCardPage = compose(
  withFirebase,
  withStyles(styles),
  withAuthorization(condition)
)(HomeworkCardPageBase);

export default HomeworkCardPage;
