import app from "firebase/app";
import "firebase/auth";
import "firebase/database";
import "firebase/storage";

const config = {
  apiKey: process.env.REACT_APP_API_KEY,
  authDomain: process.env.REACT_APP_AUTH_DOMAIN,
  databaseURL: process.env.REACT_APP_DATABASE_URL,
  projectId: process.env.REACT_APP_PROJECT_ID,
  storageBucket: process.env.REACT_APP_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_MESSAGING_SENDER_ID
};

class Firebase {
  constructor() {
    app.initializeApp(config);

    this.auth = app.auth();
    this.db = app.database();
    this.storage = app.storage();
    // this.storageRef = this.storage.ref();
  }

  // *** Auth API ***

  doCreateUserWithEmailAndPassword = (email, password) =>
    this.auth.createUserWithEmailAndPassword(email, password);

  doSignInWithEmailAndPassword = (email, password) =>
    this.auth.signInWithEmailAndPassword(email, password);

  doSignOut = () => this.auth.signOut();

  doPasswordReset = email => this.auth.sendPasswordResetEmail(email);

  doPasswordUpdate = password => this.auth.currentUser.updatePassword(password);

  // *** Current User ***

  currUser = () => this.auth.currentUser;

  // *** User API ***

  user = uid => this.db.ref(`users/${uid}`);

  users = () => this.db.ref(`users`);

  userClasses = uid => this.db.ref(`users/${uid}/classes`);

  userAssignments = uid => this.db.ref(`users/${uid}/assignments`);

  // *** Language API ***

  language = languageId => this.db.ref(`languages/${languageId}`);

  languages = () => this.db.ref(`languages`);

  // *** Dialects API

  dialects = languageId => this.db.ref(`languages/${languageId}/dialects`);

  // *** Word API ***

  word = (languageId, wordId) => this.db.ref(`words/${languageId}/${wordId}`);

  words = languageId => this.db.ref(`words/${languageId}`);

  // *** Class API ***

  class = classId => this.db.ref(`classes/${classId}`);

  classes = () => this.db.ref(`classes`);

  classAssignments = classId => this.db.ref(`classes/${classId}/assignments`);

  classAssignment = (classId, assignmentId) =>
    this.db.ref(`classes/${classId}/assignments/${assignmentId}`);

  classStudents = classId => this.db.ref(`classes/${classId}/students`);

  // *** Assignment API ***/

  assignment = assignmentId => this.db.ref(`assignments/${assignmentId}`);

  assignments = () => this.db.ref(`assignments`);

  // *** Word Audio API ***

  wordAudio = (languageId, wordId, dialectId) =>
    this.storage.ref().child(`audio/${languageId}/${wordId}/${dialectId}`);

  // *** Word Image API ***

  wordImage = (languageId, wordId, dialectId) =>
    this.storage.ref().child(`image/${languageId}/${wordId}/${dialectId}`);

  // *** Merge Auth and DB User API *** //

  onAuthUserListener = (next, fallback) =>
    this.auth.onAuthStateChanged(authUser => {
      if (authUser) {
        this.user(authUser.uid)
          .once("value")
          .then(snapshot => {
            const dbUser = snapshot.val();
            // merge auth and db user
            authUser = {
              uid: authUser.uid,
              email: authUser.email,
              ...dbUser
            };
            next(authUser);
          });
      } else {
        fallback();
      }
    });
}
export default Firebase;
