import React, { Component } from "react";
import { compose } from "recompose";
import { withStyles } from "@material-ui/styles";
import {
  Grid,
  TextField,
  Typography,
  Button,
  Select,
  InputLabel
} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import { withFirebase } from "../Firebase";

import * as ROLES from "../../constants/roles";

const styles = theme => ({
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  input: {
    display: "none"
  }
});

const INITIAL_STATE = {
  title: "",
  languages: [],
  selectedLanguage: "",
  students: [],
  selectedStudents: [],
  error: null
};

class ClassFormBase extends Component {
  constructor(props) {
    super(props);
    this.state = { ...INITIAL_STATE, loading: false };
  }

  componentDidMount() {
    this.setState({ loading: true });

    this.props.firebase.users().on("value", snapshot => {
      const studentsObject = snapshot.val();

      const studentsList = Object.keys(studentsObject)
        .map(key => ({
          ...studentsObject[key],
          uid: key
        }))
        .filter(student => student.role === ROLES.STUDENT);

      this.setState({
        students: studentsList,
        loading: false
      });
    });

    this.props.firebase.languages().on("value", snapshot => {
      const languagesObject = snapshot.val();

      const languagesList = Object.keys(languagesObject).map(key => ({
        ...languagesObject[key],
        languageId: key
      }));

      this.setState({
        languages: languagesList,
        loading: false
      });
    });
  }

  componentWillUnmount() {
    this.props.firebase.users().off();
    this.props.firebase.languages().off();
  }

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  onChangeAutocomplete = (event, value) => {
    this.setState({ selectedStudents: value });
  };

  onSubmit = event => {
    const { firebase } = this.props;
    const { title, selectedLanguage, selectedStudents } = this.state;
    const teacherId = firebase.currUser().uid;
    const studentsId = selectedStudents.map(student => student.uid);

    firebase
      .classes()
      .push({
        title: title,
        languageId: selectedLanguage,
        teacher: teacherId,
        students: studentsId
      })
      .then(_class => {
        const classId = _class.key;

        firebase
          .userClasses(teacherId)
          .child(classId)
          .set({
            title,
            languageId: selectedLanguage
          });

        studentsId.forEach(studentId => {
          firebase
            .userClasses(studentId)
            .child(classId)
            .set({ title, languageId: selectedLanguage });
        });
      })
      .then(() => {
        this.setState({ ...INITIAL_STATE });
      })
      .catch(error => {
        this.setState({ error });
      });

    event.preventDefault();
  };

  render() {
    const { classes } = this.props;

    const {
      title,
      languages,

      selectedLanguage,
      students,
      selectedStudents,
      loading,
      error
    } = this.state;

    const isInvalid = title === "" || selectedLanguage === "";

    return (
      <form className={classes.form} noValidate onSubmit={this.onSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              autoFocus
              fullWidth
              id="title"
              label="Class name"
              name="title"
              value={title}
              onChange={this.onChange}
            />
          </Grid>

          <Grid item xs={12}>
            <InputLabel required id="languageLabel">
              Language
            </InputLabel>
            <Select
              native
              value={selectedLanguage}
              name="selectedLanguage"
              labelId="languageLabel"
              variant="outlined"
              fullWidth
              onChange={this.onChange}
            >
              <option value="" />
              {languages &&
                languages.map(language => (
                  <option key={language.languageId} value={language.languageId}>
                    {language.name}
                  </option>
                ))}
            </Select>
          </Grid>
          <Grid item xs={12}>
            <Autocomplete
              multiple
              value={selectedStudents}
              options={students}
              getOptionLabel={option => `${option.first} ${option.last}`}
              filterSelectedOptions
              loading={loading}
              onChange={this.onChangeAutocomplete}
              renderInput={params => (
                <TextField
                  {...params}
                  variant="outlined"
                  margin="normal"
                  label="Students"
                  fullWidth
                />
              )}
            />
          </Grid>
          <Grid item xs={12}>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              disabled={isInvalid}
            >
              Create
            </Button>
            {error && <Typography color="error">{error.message}</Typography>}
          </Grid>
        </Grid>
      </form>
    );
  }
}

const ClassForm = compose(withFirebase, withStyles(styles))(ClassFormBase);

export default ClassForm;
