import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { withStyles } from "@material-ui/styles";
import { Grid, Typography, Container } from "@material-ui/core";

import { withFirebase } from "../Firebase";
import { compose } from "recompose";
import SimpleCard from "../CardFunction";
import * as ROUTES from "../../constants/routes";
import { withAuthorization } from "../Session";
import * as ROLES from "../../constants/roles";

const styles = theme => ({
  root: {
    height: "100vh"
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8)
  },
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column"
  },
  cardMedia: {
    paddingTop: "56.25%" // 16:9
  },
  cardContent: {
    flexGrow: 1
  }
});

class TeacherPortalPageBase extends Component {
  render() {
    const { classes } = this.props;

    return (
      <div>
        <Container className={classes.cardGrid} maxWidth="md">
          <Grid container spacing={4}>
            <SimpleCard
              image="/images/homework.jpg"
              title="first title"
              link={ROUTES.HOMEWORK}
            >
              <Typography gutterBottom variant="h5" component="h2">
                Homework
              </Typography>
              <Typography>Create a new Homework</Typography>
            </SimpleCard>
            <SimpleCard
              image="/images/word.jpg"
              title="second title"
              link={ROUTES.WORD}
            >
              <Typography gutterBottom variant="h5" component="h2">
                Word
              </Typography>
              <Typography>Add a new Word</Typography>
            </SimpleCard>
            <SimpleCard
              image="/images/grading.gif"
              title="third title"
              link={ROUTES.GRADING}
            >
              <Typography gutterBottom variant="h5" component="h2">
                Grading
              </Typography>
              <Typography>Grade your Homework</Typography>
            </SimpleCard>
            <SimpleCard
              image="/images/class.jpg"
              title="Create a class"
              link={ROUTES.CLASS}
            >
              <Typography gutterBottom variant="h5" component="h2">
                Class
              </Typography>
              <Typography>Create a new class</Typography>
            </SimpleCard>
            <SimpleCard
              image="/images/homework.jpg"
              title="list of words"
              link={ROUTES.WORD_LIST}
            >
              <Typography gutterBottom variant="h5" component="h2">
                List of Words
              </Typography>
              <Typography>words</Typography>
            </SimpleCard>
            <SimpleCard
              image="/images/homework.jpg"
              title="Language"
              link={ROUTES.LANGUAGE}
            >
              <Typography gutterBottom variant="h5" component="h2">
                Language
              </Typography>
              <Typography>Add a New Language/Dialect</Typography>
            </SimpleCard>
          </Grid>
        </Container>
      </div>
    );
  }
}

const condition = authUser => !!authUser && authUser.role === ROLES.TEACHER;

const TeacherPortalPage = compose(
  withRouter,
  withFirebase,
  withStyles(styles),
  withAuthorization(condition)
)(TeacherPortalPageBase);

export default TeacherPortalPage;
