import React, { Component } from "react";
import { compose } from "recompose";
import { withFirebase } from "../Firebase";
import { withStyles } from "@material-ui/core";
import { Container, Typography, CssBaseline } from "@material-ui/core";
import PracticeFragment from "./practicefragment";
import { withAuthorization } from "../Session";

const styles = theme => ({
  root: {
    height: "100vh"
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  }
});

class WordPracticePageBase extends Component {
  render() {
    const { classes } = this.props;

    const { languageId, wordId } = this.props.match.params;

    return (
      <Container component="main" maxWidth="sm" className={classes.root}>
        <CssBaseline />
        <div className={classes.paper}>
          <Typography component="h1" variant="h6">
            Practice the word
          </Typography>
          <PracticeFragment languageId={languageId} wordId={wordId} />
        </div>
      </Container>
    );
  }
}

const condition = authUser => !!authUser;

const WordPracticePage = compose(
  withFirebase,
  withAuthorization(condition),
  withStyles(styles)
)(WordPracticePageBase);

export default WordPracticePage;
