import React, { Component } from "react";
import { compose } from "recompose";
import { withStyles } from "@material-ui/styles";
import {
  Container,
  CssBaseline,
  Typography,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
  Grid,
  InputLabel,
  Select,
  Button,
  List,
  ListItem
} from "@material-ui/core";
import { Link, withRouter } from "react-router-dom";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

import { withFirebase } from "../Firebase";
import * as STATUS from "../../constants/assignmentstatus";
import * as ROUTES from "../../constants/routes";
import * as ROLES from "../../constants/roles";
import { withAuthorization } from "../Session";

const styles = theme => ({
  root: {
    height: "100vh"
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  form: {
    margin: theme.spacing(4, 4)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  panel: {
    width: "100%"
  }
});

const INITIAL_STATE = {
  uid: "",
  _classes: [],
  selectedClass: "",
  assignments: [],
  isDisabled: true,
  error: null
};

const generateList = (_class, assignments, status) => {
  return (
    <List>
      {assignments
        .filter(assignment => assignment.status === status)
        .map(assignment => (
          <ListItem
            button={true}
            component={Link}
            to={`${ROUTES.HOMEWORK_INFO}/${_class}/${assignment.assignmentId}`}
            key={`${assignment.assignmentId}`}
          >
            {assignment.title}
          </ListItem>
        ))}
    </List>
  );
};

class StudentHomeworkPageBase extends Component {
  constructor(props) {
    super(props);

    this.state = { ...INITIAL_STATE, loading: false };
  }

  componentDidMount() {
    this.setState({ loading: true });

    this.unsubscribe = this.props.firebase.auth.onAuthStateChanged(currUser => {
      this.props.firebase.userClasses(currUser.uid).on("value", snapshot => {
        const classesObject = snapshot.val();

        const classesList = Object.keys(classesObject).map(key => ({
          ...classesObject[key],
          classId: key
        }));

        this.setState({
          _classes: classesList,
          loading: false,
          uid: currUser.uid
        });
      });
    });
  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  onSubmit = event => {
    const { uid, selectedClass } = this.state;

    this.props.firebase.userAssignments(uid).on("value", snapshot => {
      const assignmentsObject = snapshot.val();

      const assignmentsList = Object.keys(assignmentsObject)
        .map(key => ({
          ...assignmentsObject[key],
          assignmentId: key
        }))
        .filter(assignment => assignment.classId === selectedClass);

      this.setState({
        assignments: assignmentsList
      });
    });

    this.setState({ isDisabled: false });
    event.preventDefault();
  };

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  render() {
    const { classes } = this.props;

    const { selectedClass, _classes, assignments, isDisabled } = this.state;

    const isSelected = !selectedClass;

    return (
      <Container component="main" maxWidth="sm" className={classes.root}>
        <CssBaseline />
        <form className={classes.form} noValidate onSubmit={this.onSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={8}>
              <InputLabel id="classLabel">Class</InputLabel>
              <Select
                native
                value={selectedClass}
                name="selectedClass"
                labelId="classLabel"
                variant="outlined"
                fullWidth
                onChange={this.onChange}
              >
                <option value="" />
                {_classes.map(_class => (
                  <option key={_class.classId} value={_class.classId}>
                    {_class.title} - {_class.language}
                  </option>
                ))}
              </Select>
            </Grid>
            <Grid item xs={12} sm={4}>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                disabled={isSelected}
              >
                Show
              </Button>
            </Grid>
          </Grid>
        </form>
        <div className={classes.paper}>
          <ExpansionPanel disabled={isDisabled} className={classes.panel}>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <Typography color="error">NEW</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <List>
                {generateList(selectedClass, assignments, STATUS.NEW)}
              </List>
            </ExpansionPanelDetails>
          </ExpansionPanel>
          <ExpansionPanel disabled={isDisabled} className={classes.panel}>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <Typography color="secondary">SUBMITTED</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <List>
                {generateList(selectedClass, assignments, STATUS.SUBMITTED)}
              </List>
            </ExpansionPanelDetails>
          </ExpansionPanel>
          <ExpansionPanel disabled={isDisabled} className={classes.panel}>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <Typography color="primary">GRADED</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <List>
                {generateList(selectedClass, assignments, STATUS.GRADED)}
              </List>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </div>
      </Container>
    );
  }
}

const condition = authUser => !!authUser && authUser.role === ROLES.STUDENT;

const StudentHomeworkPage = compose(
  withRouter,
  withFirebase,
  withStyles(styles),
  withAuthorization(condition)
)(StudentHomeworkPageBase);

export default StudentHomeworkPage;
